import React, {useState} from "react";
import {useNavigate} from 'react-router-dom';
import { Link } from 'react-router-dom';
import { signInWithPopup, signOut } from 'firebase/auth';
import { auth, googleProvider, kakaoProvider } from './sign/firebase';

const LoginPage = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        if(username === 'admin' && password === 'password') {
            navigate('/mypage');
        } else{
            alert('    .');
        }
    };

    const handleGoogleLogin = async () => {
        try {
            const result = await signInWithPopup(auth, googleProvider);
            const user = result.user;
            console.log('Google  :', user);
            alert(`, ${user.displayName}!`);
            navigate('/mypage');
        } catch (error) {
            console.error('Google  :', error);
            alert('Google  .');
        }
    };

    const handleNaverLogin = async () => {
        const state = Math.random().toString(36).substring(2, 15) +
                     Math.random().toString(36).substring(2, 15);
        window.localStorage.setItem('naverState', state);

        const params = [
            'response_type=code',
            `client_id=${process.env.REACT_APP_NAVER_CLIENT_ID}`,
            `redirect_uri=${window.location.origin}/callback/naver`,
            `state=${state}`
        ].join('&');
        
        window.location.href = `https://nid.naver.com/oauth2.0/authorize?${params}`;
    };

    const handleKakaoLogin = async () => {
        try {
            const result = await signInWithPopup(auth, kakaoProvider);
            const user = result.user;
            console.log('  :', user);
            alert(`, ${user.displayName || user.email}!`);
            navigate('/mypage');
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    return (
        <div className="login-container">
            <div className="login-page">
                <h2 className="login-title"></h2>
                <div className="divider" />
                <form onSubmit={handleSubmit} className="login-form">
                    <div className="form-group">
                        <label htmlFor="username"></label>
                        <input
                            type="text"
                            id="username"
                            value={username}
                            placeholder=" "
                            onChange={(e) => setUsername(e.target.value)}
                            required
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="password"></label>
                        <input
                        type="password"
                        id="password"
                        value={password}
                        placeholder=" "
                        onChange={(e) => setPassword(e.target.value)}
                        required
                        />
                    </div>
                    <button type="submit" className="submit-button"></button>
                </form>
                <div className="sub-links">
                    <Link to="/register"></Link>
                    <span>|</span>
                    <Link to="/find-id-password">/ </Link>
                </div>
                <div className="divider" />
                <div className="social-title"> </div>
                <div className="social-buttons">
                    <button className="social-button kakao" onClick={handleKakaoLogin} title=" " aria-label=" "></button> 
                    <button className="social-button naver" onClick={handleNaverLogin} title=" " aria-label=" "></button>
                    <button className="social-button google" onClick={handleGoogleLogin} title=" " aria-label=" "></button>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;