import React,{useState} from "react";
import axios from "axios";
import {useNavigate} from "react-router-dom";

const RegisterPage = () => {
    const navigate = useNavigate();

        const [form, setForm] = useState({
            name: '',
            password: '',
            email: '',
        });
        
        const handleChange = (e) => {
            const {name, value} = e.target;
            setForm({...form, [name]: value});
        };

        const handleSubmit = async (e) => {
            e.preventDefault();
            try{
                await axios.post(`${process.env.REACT_APP_API_URL}/api/users`, form);
                alert(' ');
                navigate('/login');
            }catch(error){
                console.error(error);
                alert(' ' + (error.response?.error || error.message));
            }
        };

        return (
            <div className="register">
                <div className="register-container">
                    <h2>   </h2>
                    <div className="divider" />
                    <form onSubmit={handleSubmit} className="register-form">
                        <div className="form-group">
                            <label htmlFor="reg-name"></label>
                            <input
                                id="reg-name"
                                type="text"
                                name="name"
                                value={form.name}
                                onChange={handleChange}
                                placeholder=" "
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="reg-id"></label>
                            <input
                                id="reg-id"
                                type="text"
                                name="id"
                                value={form.id}
                                onChange={handleChange}
                                placeholder=" "
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="reg-password"></label>
                            <input
                                id="reg-password"
                                type="password"
                                name="password"
                                value={form.password}
                                onChange={handleChange}
                                placeholder=" "
                                required
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="reg-email"></label>
                            <input
                                id="reg-email"
                                type="email"
                                name="email"
                                value={form.email}
                                onChange={handleChange}
                                placeholder=" "
                                required
                            />
                        </div>
                        <button type="submit" className="submit-button"></button>
                    </form>
                </div>
            </div>
        );
    };


export default RegisterPage;