import React, { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { signInWithCustomToken } from 'firebase/auth';
import { auth } from './sign/firebase';
import axios from 'axios';

const NaverCallback = () => {
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const handleNaverCallback = async () => {
            try {
                // URL  
                const code = searchParams.get('code');
                const state = searchParams.get('state');
                const error = searchParams.get('error');

                //  
                if (error) {
                    throw new Error(`  : ${error}`);
                }

                if (!code) {
                    throw new Error('  .');
                }

                // State 
                const savedState = localStorage.getItem('naverState');
                if (state !== savedState) {
                    throw new Error('State   .');
                }

                // localStorage state 
                localStorage.removeItem('naverState');

                // Node.js  API 
                const response = await axios.post('/api/naver-auth/exchange-token', {
                    code: code
                });

                //   Firebase  
                const { firebaseToken } = response.data.data;

                // Firebase   
                await signInWithCustomToken(auth, firebaseToken);

                //   -  
                alert('  !');
                navigate('/mypage');

            } catch (error) {
                console.error('   :', error);
                setError(error.message);
                
                // 3    
                setTimeout(() => {
                    navigate('/login');
                }, 3000);
            } finally {
                setLoading(false);
            }
        };

        handleNaverCallback();
    }, [searchParams, navigate]);

    if (loading) {
        return (
            <div style={{ 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center', 
                height: '100vh',
                flexDirection: 'column'
            }}>
                <h2>   ...</h2>
                <p> .</p>
            </div>
        );
    }

    if (error) {
        return (
            <div style={{ 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center', 
                height: '100vh',
                flexDirection: 'column'
            }}>
                <h2> </h2>
                <p style={{ color: 'red' }}>{error}</p>
                <p>3    ...</p>
            </div>
        );
    }

    return null;
};

export default NaverCallback;
