import React, { useState, useEffect} from "react"; //  React Router
import { useNavigate } from 'react-router-dom';
import { onAuthStateChanged, signOut } from 'firebase/auth'; //Firebase   , SignOut firebase  
import { auth } from './sign/firebase'; //Firebase  

const Mypage = () => {
    const navigate = useNavigate();
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
            if (currentUser) {
                setUser({
                    username: currentUser.displayName || ' ',
                    email: currentUser.email,
                    photoURL: currentUser.photoURL,
                    uid: currentUser.uid
                });
            } else {
                //      
                navigate('/login');
            }
            setLoading(false);
        });

        return () => unsubscribe();
    }, [navigate]);

    const handleLogout = async () => {
        try {
            await signOut(auth);
            alert('.');
            navigate('/login');
        } catch (error) {
            console.error(' :', error);
            alert(' .');
        }
    };

    if (loading) {
        return (
            <div className="mypage">
                <h2> ...</h2>
            </div>
        );
    }

    if (!user) {
        return null; //     
    }

    return (
        <div className="mypage">
            <div className="user-info">
                <h2 className="mypage-title"></h2>
                {user.photoURL && (
                    <div className="user-avatar" style={{ textAlign: 'center' }}>
                        <img 
                            src={user.photoURL} 
                            alt=" " 
                            style={{
                                width: '80px', 
                                height: '80px', 
                                borderRadius: '50%',
                                marginBottom: '20px',
                                display: 'block',
                                margin: '0 auto 20px auto'
                            }}
                        />
                    </div>
                )}
                <p>
                    <strong className="user_name">:</strong> {user.username}
                </p>
                <p>
                    <strong className="user_email">:</strong> {user.email}
                </p>
                <p>
                    <strong> ID:</strong> {user.uid}
                </p>
                <div className="logout-section">
                    <button 
                        onClick={handleLogout}
                        className="logout-button"
                        style={{
                            backgroundColor: '#78509D',
                            color: 'white',
                            border: 'none',
                            padding: '10px 20px',
                            borderRadius: '5px',
                            cursor: 'pointer',
                            marginTop: '20px'
                        }}
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    );
};

export default Mypage;