import React from "react";
import { NavLink } from "react-router-dom";
import { FiHome, FiCalendar, FiUsers, FiBell, FiSettings } from "react-icons/fi";

const SideBar = ({ sidebarOpen = false, onClose }) => {
  const items = [
    { to: "/", icon: <FiHome />, label: "" },
    { to: "/MySchedule", icon: <FiCalendar />, label: "" },
    { to: "/Friend", icon: <FiUsers />, label: "" },
    { to: "/Notifications", icon: <FiBell />, label: "" },
    { to: "/Setting", icon: <FiSettings />, label: "" },
  ];

  return (
    <aside className={`sidebar${sidebarOpen ? " open" : ""}`} aria-label="">
      <nav className="sidebar-nav">
        {items.map((item) => (
          <NavLink
            key={item.to}
            to={item.to}
            className={({ isActive }) =>
              `side-item${isActive ? " active" : ""}`
            }
            title={item.label}
            end
            onClick={() => onClose && onClose()}
          >
            {item.icon}
          </NavLink>
        ))}
      </nav>
    </aside>
  );
};

export default SideBar; 